import { notFound } from 'next/navigation';

type Quiz = {
  id: number;
  title: string;
};

async function getQuiz(id: string): Promise<Quiz | null> {
  try {
    const res = await fetch(`http://localhost:3001/quizzes`);
    const quizzes = await res.json();
    return quizzes.find((q: Quiz) => q.id.toString() === id) || null;
  } catch (error) {
    console.error(error);
    return null;
  }
}

export default async function QuizPage({ params }: { params: { id: string } }) {
  const quiz = await getQuiz(params.id);

  if (!quiz) return notFound();

  return (
    <div className='p-6 max-w-2xl mx-auto'>
      <h1 className='text-2xl font-bold mb-4'>Quiz: {quiz.title}</h1>
      <p>This is where the questions will go ðŸ‘‡</p>
      {/* We'll render questions here next */}
    </div>
  );
}
